

events.on('ready', function() {
    carregarTela();
    $('.botao').on('click', function() {
    
        $(this).removeClass('wow animated');
        $(this).addClass('visited');
        $(this).find('.card-color').css('background-color', '#8696BC');
    
        if ($('.botao').length === $('.botao.visited').length) {
            scorm.saveObject('pag07', true);
        }
    
    });
    function carregarTela() {
        var load = scorm.loadObject('pag07');
        if(load == true ) {
            $('.botao').removeClass('wow animated');
            $('.botao').addClass('visited');
            $('.card-color').css('background-color', '#8696BC');
            $('.arrow-right').removeClass('hide');
        }
    }
});